import axios from "axios"

axios.defaults.baseURL = 'http://localhost:5000/api';

const usersUrl = "/users";
const userUrl = "/user";
const newUserUrl = "/newUser";

const getUser_NVUrl = "/getUserNV";

const linksUrl = "/links";
const linkUrl = "/link";

const tasksUrl = "/tasks";
const taskUrl = "/task";

const notificationsUrl = "/notifications";

export default class API {

    // users

    static async getAllUsers() {
        const res = await axios.get(usersUrl);
        return res.data;
    }

    static async getUser(user) {
        const res = await axios.post(userUrl, user)
        return res.data;
    }

    static async newUser(newUser) {
        const res = await axios.post(newUserUrl, newUser)
        return res.data;
    }

    static async getUserNV(user) {
        const res = await axios.post(getUser_NVUrl, user)
        return res.data;
    }

    static async changePassword(passwords) {
        const res = await axios.patch(userUrl, { data: passwords })
        return res.data;
    }

    // links

    static async getAllLinks() {
        const res = await axios.get(linksUrl);
        return res.data;
    }

    static async postLink(link) {
        const res = await axios.post(linkUrl, link);
        return res.data;
    }

    static async deleteLink(video) {
        const res = await axios.delete(linkUrl, { data: video });
        return res.data;
    }

    // tasks

    static async getAllTasks() {
        const res = await axios.get(tasksUrl);
        return res.data;
    }

    static async postTask(task) {
        const res = await axios.post(taskUrl, task);
        return res.data;
    }

    static async deleteTask(task) {
        const res = await axios.delete(taskUrl, { data: task });
        return res.data;
    }

    static async updateTask(task) {
        const res = await axios.patch(taskUrl, { data: task });
        return res.data;
    }

    // notifications

    static async getAllNotifications() {
        const res = await axios.get(notificationsUrl);
        return res.data;
    }
}