<template>
  <div v-if="this.accountStore.isLogged" id="userCard" :style="userCardStyle">
    <div
      @click="cardState == 0 ? (cardState = 1) : (cardState = 0)"
      id="userImage"
      class="pointer"
    >
      <img
        class="staticImg"

        src="../../assets/userAvatar.png"
        alt="User Avatar"
      />
    </div>
    <div :style="userDetails" id="userDetails">
      <svg-icon
        @click="cardState == 1 ? (cardState = 2) : (cardState = 1)"
        id="userArrow"
        class="pointer"
        width="30px"
        height="30px"
        type="mdi"
        :style="userArrow"
        :path="iconsStore.mdiChevronLeft"
      ></svg-icon>
      <div id="userInfo">
        <span id="userNickname">{{ accountStore.userNickname }}</span>
        <span id="userRole">{{ accountStore.userRoles[0] }}</span>
      </div>
    </div>
    <div :style="userOptions" id="userOptions">
      <svg-icon
        @click="logout()"
        class="pointer"
        type="mdi"
        style="--r: 180deg"
        :path="iconsStore.mdiLogout"
      ></svg-icon>
      <hr />
      <router-link to="/dashboard/profil">
        <svg-icon
          type="mdi"
          :path="iconsStore.mdiAccountOutline"
        ></svg-icon>
      </router-link>
    </div>
  </div>
</template>

<script>
import svgIcon from "@jamescoyle/vue-icon";
import popupSound from "../../assets/audio/popup.wav";

import { useAlertsStore } from "../../stores/alertsStore";
import { useAccountStore } from "../../stores/accountStore";
import { usePreferenceStore } from "../../stores/preferenceStore";
import { useIconsStore } from "../../stores/iconsStore";

export default {
  name: "UserCard",
  components: {
    svgIcon,
  },
  setup() {
    const accountStore = useAccountStore();
    const preferenceStore = usePreferenceStore();
    const alertsStore = useAlertsStore();
    const iconsStore = useIconsStore();

    return { accountStore,preferenceStore,  alertsStore, iconsStore };
  },
  data() {
    return {
      cardState: 0,
      userCardStyle: {
        borderRadius: "25px",
      },
      userDetails: {
        visibility: "hidden",
        maxWidth: "0px",
        marginRight: "0px",
      },
      userArrow: {
        "--r": "0deg",
      },
      userOptions: {
        visibility: "hidden",
        maxWidth: "0px",
        paddingLeft: "0px",
      },
    };
  },
  methods: {
    async logout() {
      this.cardState = 0,

      this.accountStore.deleteCookies();

      this.$router.push("/");

      await this.alertsStore.showNotification(
        4,
        "success",
        "Pomyślnie wylogowano"
      );

      if (this.preferenceStore.preferences[0].state) {
        const notificationSound = new Audio(popupSound);
        notificationSound.volume = 0.5;
        await notificationSound.play();
      }
    },
  },
  watch: {
    cardState() {
      if (this.cardState == 1) {
        this.userCardStyle = {
          borderRadius: "10px 25px 25px 10px",
        };
        this.userDetails = {
          visibility: "visible",
          maxWidth: "150px",
          marginRight: "20px",
        };
        this.userArrow = {
          "--r": "0deg",
        };
        this.userOptions = {
          visibility: "hidden",
          maxWidth: "0px",
          paddingLeft: "0px",
        };
      } else if (this.cardState == 2) {
        this.userArrow = {
          "--r": "180deg",
        };
        this.userOptions = {
          visibility: "visible",
          maxWidth: "250px",
          paddingLeft: "10px",
        };
      } else {
        this.userCardStyle = {
          borderRadius: "25px",
        };
        this.userDetails = {
          visibility: "hidden",
          maxWidth: "0px",
          marginRight: "0px",
        };
        this.userArrow = {
          "--r": "0deg",
        };
        this.userOptions = {
          visibility: "hidden",
          maxWidth: "0px",
          paddingLeft: "0px",
        };
      }
    },
  },
};
</script>

<style lang="scss" scoped>
$transition: 0.2s ease-in-out;

#userCard {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-flow: row-reverse nowrap;
  height: 50px;
  background-color: #272727;
  border-radius: 10px 25px 25px 10px;
  box-shadow: 0 4px 4px rgb(0 0 0 / 25%);
  transition: $transition;
  user-select: none;

  #userImage {
    img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      box-shadow: 0 0 5px #9f9f9f;
    }
  }

  #userDetails {
    margin-right: 20px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    transition: $transition;
    overflow: hidden;

    #userArrow {
      margin: auto 10px;

      &:hover {
        color: #999;
      }
    }

    #userInfo {
      display: flex;
      flex-flow: column nowrap;
      justify-content: center;
      align-items: flex-start;
      min-width: 50px;

      #userNickname {
        font-size: 18px;
      }

      #userRole {
        color: #999;
        font-size: 12px;
        text-transform: uppercase;
      }
    }
  }

  #userOptions {
    display: flex;
    flex-flow: row nowrap;
    overflow: hidden;
    transition: $transition;

    hr {
      width: 1px;
      height: 20px;
      border: none;
      background-color: #43444a;
      margin: auto 10px;
    }

    a,
    svg {
      width: 24px;
      height: 24px;
      transition: 0.1s ease-in-out;

      &:hover {
        color: #77839c;
      }
    }
  }
}
</style>