import { defineStore } from 'pinia'

import API from "../api";

export const useGeneralStore = defineStore('generalStore', {
    state: () => ({
        activeTaskElement: "home",
        statistics: {
            users: undefined,
            links: undefined,
            tasks: undefined,
            tasksStatistics: {
                general: undefined,
                available: undefined,
                inProgress: undefined,
                finished: undefined,
                cancelled: undefined,
                important: undefined,
            }
        }
    }),
    actions: {
        async getStatistics() {
            const usersLength = await API.getAllUsers();
            this.statistics.users = usersLength.length;

            const linksLength = await API.getAllLinks();
            this.statistics.links = linksLength.length;

            const tasksLength = await API.getAllTasks();
            this.statistics.tasks = tasksLength.length;
        },

        async getTasksStatistics() {
            const tasks = await API.getAllTasks();
            this.statistics.tasksStatistics.general = tasks.length;

            const availableTasks = await tasks.filter(task => {
                return task.status.includes("available");
            })
            this.statistics.tasksStatistics.available = availableTasks.length;

            const inProgressTasks = await tasks.filter(task => {
                return task.status.includes("inProgress");
            })
            this.statistics.tasksStatistics.inProgress = inProgressTasks.length;

            const finishedTasks = await tasks.filter(task => {
                return task.status.includes("finished");
            })
            this.statistics.tasksStatistics.finished = finishedTasks.length;

            const cancelledTasks = await tasks.filter(task => {
                return task.status.includes("cancelled");
            })
            this.statistics.tasksStatistics.cancelled = cancelledTasks.length;

            const importantTasks = await tasks.filter(task => {
                return task.important === true;
            })
            this.statistics.tasksStatistics.important = importantTasks.length;
        },

        async sendVideo(data) {
            return await API.postLink(data);
        },

        async addTask(data) {
            return await API.postTask(data);
        }
    }
})