import { defineStore } from 'pinia'

import {
  mdiCheckCircleOutline,
  mdiAlertCircleOutline,
  mdiAlertOutline,
  mdiAlertOctagonOutline,
} from "@mdi/js";

export const useAlertsStore = defineStore('alertsStore', {
  state: () => ({
    alerts: {
      isDisplay: false,
      styles: {
        top: "-75px",
      },
      notificationIcon: "",
      notificationClass: "",
      notificationMsg: "",
      icons: {
        successIcon: mdiCheckCircleOutline,
        alertCircleIcon: mdiAlertCircleOutline,
        alertTriangleIcon: mdiAlertOutline,
        alertOctagonIcon: mdiAlertOctagonOutline,
      }
    }
  }),
  actions: {
    showNotification(duration, status, message) {
      this.alerts.isDisplay = true;
      duration *= 1000;

      setTimeout(() => {
        if (status === "success")
          this.alerts.notificationIcon = this.alerts.icons.successIcon;
        else if (status === "info")
          this.alerts.notificationIcon = this.alerts.icons.alertCircleIcon;
        else if (status === "warning")
          this.alerts.notificationIcon = this.alerts.icons.alertTriangleIcon;
        else this.alerts.notificationIcon = this.alerts.icons.alertOctagonIcon;

        this.alerts.styles.top = "25px";
        this.alerts.notificationClass = status;
        this.alerts.notificationMsg = message;
      }, 1)

      setTimeout(() => {
        this.alerts.styles.top = "-75px";
      }, duration)
      setTimeout(() => {
        this.alerts.isDisplay = false;
      }, duration + 1000)
    }
  },
})