import { defineStore } from 'pinia'

export const useLoginStore = defineStore('loginStore', {
  state: () => ({
    isShow: false,
    activeForm: 1,
    formText: "Zaloguj się",
    isButtonDisabled: true,
  }),
  actions: {
    setActiveForm(action) {
      this.isButtonDisabled = true;

      if (action === "add") this.activeForm++;
      else if (action === "subtract") this.activeForm--;

      if (this.activeForm > 2) this.activeForm = 1;
      else if (this.activeForm < 1) this.activeForm = 2;

      if (this.activeForm === 1) this.formText = "Zaloguj się";
      else if (this.activeForm === 2) this.formText = "Stwórz konto";
    },
  },
})