<template>
  <div class="formBox">
    <div id="formSignupSection">
      <FormButton />
      <div class="formInputsContainer">
        <div class="formInput formItem">
          <label for="nickInput">
            Login <span class="formError">{{ errors.nickError }}</span>
          </label>
          <div :class="nickClass" class="border">
            <input
              name="nickInput"
              type="text"
              v-model="nickInput"
              maxlength="10"
              autocomplete="off"
            />
            <div class="helpBox" title="Wpisz swój nick">
              <svg-icon
                :class="nickClass"
                type="mdi"
                :path="nickIcon"
              ></svg-icon>
            </div>
          </div>
        </div>
        <div class="formInput formItem">
          <label for="passwordInput">
            Hasło <span class="formError">{{ errors.passwordError }}</span>
          </label>
          <div :class="passwordClass" class="border">
            <input
              name="passwordInput"
              :type="passwordType"
              v-model="passwordInput"
              maxlength="20"
              autocomplete="off"
            />
            <div class="helpBox" title="Wpisz swoje hasło">
              <svg-icon
                @mouseenter="passwordType = 'text'"
                @mouseleave="passwordType = 'password'"
                :class="passwordClass"
                type="mdi"
                :path="passwordIcon"
              ></svg-icon>
            </div>
          </div>
        </div>
        <div class="formInput formItem">
          <label for="codeInput">
            Kod <span class="formError">{{ errors.codeError }}</span>
          </label>
          <div :class="codeClass" class="border">
            <input
              name="codeInput"
              type="text"
              v-model="codeInput"
              maxlength="10"
              autocomplete="off"
            />
            <div class="helpBox" title="Wpisz kod">
              <svg-icon
                :class="codeClass"
                type="mdi"
                :path="codeIcon"
              ></svg-icon>
            </div>
          </div>
        </div>
      </div>
    </div>
    <button
      @click="submitForm()"
      :disabled="loginStore.isButtonDisabled"
      id="submitBtn"
    >
      {{ loginStore.formText }}
    </button>
  </div>
</template>

<script>
import svgIcon from "@jamescoyle/vue-icon";
import popupSound from "../../assets/audio/popup.wav";

import { useIconsStore } from "../../stores/iconsStore";
import { useLoginStore } from "../../stores/loginStore";
import { useAccountStore } from "../../stores/accountStore";
import { usePreferenceStore } from "../../stores/preferenceStore";
import { useAlertsStore } from "../../stores/alertsStore";

import FormButton from "./LoginFormBtn.vue";

export default {
  name: "SigupSection",
  components: {
    svgIcon,
    FormButton,
  },
  setup() {
    const iconsStore = useIconsStore();
    const loginStore = useLoginStore();
    const accountStore = useAccountStore();
    const preferenceStore = usePreferenceStore();
    const alertsStore = useAlertsStore();

    return {
      iconsStore,
      loginStore,
      accountStore,
      preferenceStore,
      alertsStore,
    };
  },
  data() {
    return {
      passwordType: "password",
      nickIcon: this.iconsStore.questionMarkIcon,
      passwordIcon: this.iconsStore.questionMarkIcon,
      codeIcon: this.iconsStore.questionMarkIcon,
      nickInput: "",
      passwordInput: "",
      codeInput: "",
      nickClass: "",
      passwordClass: "",
      codeClass: "",
      errors: {
        nickError: "",
        passwordError: "",
        codeError: "",
      },
    };
  },
  methods: {
    async submitForm() {
      if (
        this.nickInput.length < 3 &&
        this.passwordInput.length < 3 &&
        this.codeInput.length != 10
      )
        return;

      const formValues = {
        nick: this.nickInput,
        password: this.passwordInput,
        code: this.codeInput,
      };

      await this.accountStore
        .saveData(formValues)
        .then(async (response) => {
          await this.alertsStore.showNotification(
            response.duration,
            response.status,
            response.message
          );

          if (response.status === "success") {
            this.nickInput = "";
            this.passwordInput = "";
            this.codeInput = "";
            this.loginStore.isShow = false;
            this.loginStore.isButtonDisabled = true;

            this.accountStore.saveCookies(response.content._id);
          }
        })
        .catch((error) => console.log(error));

      if (this.preferenceStore.preferences[0].state) {
        const notificationSound = new Audio(popupSound);
        notificationSound.volume = 0.5;
        await notificationSound.play();
      }
    },
    formVerification(value) {
      if (value.length >= 3) {
        return {
          icon: this.iconsStore.successIcon,
          status: "success",
          error: "",
        };
      } else if (value.length === 0) {
        return {
          icon: this.iconsStore.alertCircleIcon,
          status: "alert",
          error: "Puste pole!",
        };
      } else {
        return {
          icon: this.iconsStore.alertCircleIcon,
          status: "alert",
          error: "Minimum 3 znaki!",
        };
      }
    },
    codeVerification(value) {
      if (value.length === 10) {
        return {
          icon: this.iconsStore.successIcon,
          status: "success",
          error: "",
        };
      } else if (value.length === 0) {
        return {
          icon: this.iconsStore.alertCircleIcon,
          status: "alert",
          error: "Puste pole!",
        };
      } else {
        return {
          icon: this.iconsStore.alertCircleIcon,
          status: "alert",
          error: "Wymagane 10 znaków!",
        };
      }
    },
    buttonVerification() {
      if (
        this.nickInput.length >= 3 &&
        this.passwordInput.length >= 3 &&
        this.codeInput.length == 10
      ) {
        this.loginStore.isButtonDisabled = false;
      } else {
        this.loginStore.isButtonDisabled = true;
      }
    },
  },
  watch: {
    nickInput() {
      this.buttonVerification();
      const data = this.formVerification(this.nickInput);
      this.nickIcon = data.icon;
      this.nickClass = data.status;
      this.errors.nickError = data.error;
    },
    passwordInput() {
      this.buttonVerification();
      const data = this.formVerification(this.passwordInput);
      this.passwordIcon = data.icon;
      this.passwordClass = data.status;
      this.errors.passwordError = data.error;
    },
    codeInput() {
      this.buttonVerification();
      const data = this.codeVerification(this.codeInput);
      this.codeIcon = data.icon;
      this.codeClass = data.status;
      this.errors.codeError = data.error;
    },
  },
};
</script>

<style lang="scss" scoped>
#formSignupSection {
  height: 75%;
}
</style>