import { defineStore } from 'pinia'

import Cookies from 'js-cookie'

import {
    mdiVolumeHigh,
    mdiMotionPlayOutline,
} from "@mdi/js";

export const usePreferenceStore = defineStore('preferenceStore', {
    state: () => ({
        preferences: [
            { name: "notificationSound", icon: mdiVolumeHigh, label: "Dźwięk powiadomień", state: true },
            { name: "animatedHeader", icon: mdiMotionPlayOutline, label: "Animowane logo", state: true },
        ],
    }),
    actions: {
        getPreference(preferenceName) {
            return Cookies.get(preferenceName);
        },

        setPreference(preferenceName, preferenceValue, setCookies) {
            const preference = this.preferences.find(p => p.name === preferenceName);

            let preferenceState = preference.state;

            if (preferenceValue === undefined) {
                preferenceState = !preferenceState;

                preferenceValue = preferenceState;
            } else preferenceState = preferenceValue;

            if (preferenceState == "true") preferenceState = true
            else if (preferenceState == "false") preferenceState = false

            preference.state = preferenceState;

            if (setCookies) Cookies.set(preferenceName, preferenceValue, { expires: 365 });
        },
    },
})