import { createRouter, createWebHashHistory } from 'vue-router'

import Cookies from 'js-cookie'
import { useAccountStore } from "../stores/accountStore";
import { usePreferenceStore } from "../stores/preferenceStore";

import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/dashboard',
    name: 'dashboard',
    meta: {
      requiresAuth: true
    },
    component: () => import(/* webpackChunkName: "Dashboard" */ '../views/DashboardView.vue')
  },
  {
    path: '/dashboard/uzytkownicy',
    name: 'users',
    meta: {
      requiresAuth: true
    },
    component: () => import(/* webpackChunkName: "Users" */ '../views/UsersView.vue')
  },
  {
    path: '/dashboard/linki',
    name: 'links',
    meta: {
      requiresAuth: true
    },
    component: () => import(/* webpackChunkName: "Links" */ '../views/LinksView.vue')
  },
  {
    path: '/dashboard/zadania',
    name: 'tasks',
    meta: {
      requiresAuth: true
    },
    component: () => import(/* webpackChunkName: "Tasks" */ '../views/TasksView.vue')
  },
  {
    path: '/dashboard/zadania/:id',
    name: 'taskDetails',
    meta: {
      requiresAuth: true
    },
    component: () => import(/* webpackChunkName: "TaskDetails" */ '../views/TaskDetailsView.vue')
  },
  {
    path: '/dashboard/profil',
    name: 'profile',
    meta: {
      requiresAuth: true
    },
    component: () => import(/* webpackChunkName: "Profile" */ '../views/ProfileView.vue')
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: () => import(/* webpackChunkName: "NotFound" */ '../views/NotFoundView.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach(async (to, from, next) => {
  const accountStore = useAccountStore();
  const preferenceStore = usePreferenceStore();

  if (Cookies.get('logged')) await accountStore.getCookies('getAccount');

  if (Cookies.get('notificationSound'))
    preferenceStore.setPreference(
      'notificationSound',
      Cookies.get('notificationSound'),
      false
    );

  if (Cookies.get('animatedHeader'))
    preferenceStore.setPreference(
      'animatedHeader',
      Cookies.get('notificationSound'),
      false
    );

  if (to.matched.some(record => record.meta.requiresAuth)) {

    if (Cookies.get('logged') && accountStore.isLogged) {
      next();
    } else {
      next({ name: 'home' });
    }
  } else {
    next();
  }
});

export default router