<template>
  <div v-if="loginStore.isShow" id="formOverlayContainer">
    <div @click="showForm()" id="formOverlay" class="pointer"></div>
    <div id="formContainer">
      <div>
        <LoginSection v-if="loginStore.activeForm === 1" />
        <SigupSection v-if="loginStore.activeForm === 2" />
      </div>
      <div id="FormVideoSection"></div>
    </div>
  </div>
</template>

<script>
import { useLoginStore } from "../../stores/loginStore";

import LoginSection from "./LoginSection.vue";
import SigupSection from "./SigupSection.vue";

export default {
  name: "OverlayForm",
  components: {
    LoginSection,
    SigupSection,
  },
  setup() {
    const loginStore = useLoginStore();

    return { loginStore };
  },
  methods: {
    showForm() {
      this.loginStore.isShow = false;
    },
  },
};
</script>

<style lang="scss">
.formBox {
  height: 100%;
}

#formOverlayContainer {
  position: absolute;
  inset: 0;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
#formOverlay {
  width: 100%;
  height: 100%;
  opacity: 75%;
  background-color: #1d2026;
}
#formContainer {
  position: absolute;
  display: flex;
  flex-flow: row nowrap;
  height: 500px;
  border-radius: 5px;
  border: 10px solid #1f2329;
  background-color: #23272e;
  box-shadow: 0 4px 4px #17191d;

  .formInputsContainer {
    padding: 0 50px;
  }

  #FormVideoSection {
    width: 300px;
    height: 100%;
    background-color: #000;
    border-left: 10px solid #1f2329;
    overflow: hidden;
  }

  #submitBtn {
    width: 125px;
    height: 30px;
    font-size: 15px;
    border: 2px solid #67ee75;
    border-radius: 14px;
    background: linear-gradient(to bottom right, #67ee75, #11b622);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    &:hover {
      border: 2px solid #39a945;
      background: linear-gradient(to bottom right, #39a945, #0c7a17);
      background-clip: text;
      -webkit-background-clip: text;
    }

    &:disabled {
      border: 2px solid #525862;
      background: linear-gradient(to bottom right, #636770, #525862);
      background-clip: text;
      -webkit-background-clip: text;
      cursor: default;
    }
  }
}
</style>