<template>
  <div id="formChangeSectionButton">
    <div @click="loginStore.setActiveForm('add')" class="arrowLeft pointer">
      <svg-icon
        width="50px"
        height="50px"
        type="mdi"
        :path="iconsStore.mdiChevronLeft"
      ></svg-icon>
    </div>
    <span>{{ loginStore.formText }}</span>
    <div
      @click="loginStore.setActiveForm('subtract')"
      class="arrowRight pointer"
    >
      <svg-icon
        width="50px"
        height="50px"
        type="mdi"
        :path="iconsStore.mdiChevronRight"
      ></svg-icon>
    </div>
  </div>
</template>

<script>
import svgIcon from "@jamescoyle/vue-icon";

import { useLoginStore } from "../../stores/loginStore";
import { useIconsStore } from "../../stores/iconsStore";

export default {
  name: "FormButton",
  components: {
    svgIcon,
  },
  setup() {
    const loginStore = useLoginStore();
    const iconsStore = useIconsStore();

    return { loginStore, iconsStore };
  },
};
</script>

<style lang="scss" scoped>
#formChangeSectionButton {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row nowrap;
  margin: 50px auto;
  user-select: none;

  span {
    width: 125px;
    margin: auto 10px;
    font-size: 20px;
  }

  div {
    transition: 0.3s ease-in-out;

    &:hover {
      color: #3b90e0;
    }
  }
}
</style>