import { defineStore } from 'pinia'

import Cookies from 'js-cookie'

import API from "../api";

export const useAccountStore = defineStore('accountStore', {
    state: () => ({
        isLogged: false,
        userID: 0,
        userNickname: "",
        userRoles: [],
        userCreatedAt: "",
    }),
    actions: {
        async getData(data) {
            const response = await API.getUser(data);

            if (response.status === "success") {
                this.isLogged = true;

                this.userID = response.content._id;
                this.userNickname = response.content.nick;
                this.userRoles = response.content.roles;

                const createdDate = new Date(response.content.createdAt);

                let createdDateDay = createdDate.getDate();
                let createdDateMonth = createdDate.getMonth() + 1;
                let createdDateYear = createdDate.getFullYear();

                if (createdDateDay < 10) createdDateDay = "0" + createdDateDay;
                if (createdDateMonth < 10) createdDateMonth = "0" + createdDateMonth;

                this.userCreatedAt = `${createdDateDay}.${createdDateMonth}.${createdDateYear}`;
            }

            return response
        },
        async saveData(data) {
            const response = await API.newUser(data);

            if (response.status === "success") {
                this.isLogged = true;

                this.userID = response.content._id;
                this.userNickname = response.content.nick;
                this.userRoles = response.content.roles;

                const createdDate = new Date(response.content.createdAt);

                let createdDateDay = createdDate.getDate();
                let createdDateMonth = createdDate.getMonth() + 1;
                let createdDateYear = createdDate.getFullYear();

                if (createdDateDay < 10) createdDateDay = "0" + createdDateDay;
                if (createdDateMonth < 10) createdDateMonth = "0" + createdDateMonth;

                this.userCreatedAt = `${createdDateDay}.${createdDateMonth}.${createdDateYear}`;
            }

            return response
        },

        saveCookies(data) {
            Cookies.set("logged", data, { expires: 365 });
        },

        async getCookies() {
            const user = Cookies.get("logged");

            if (user) {
                const response = await API.getUserNV({ user: user });

                if (response.status === "success") {
                    this.isLogged = true;

                    this.userID = response.content._id;
                    this.userNickname = response.content.nick;
                    this.userRoles = response.content.roles;

                    const createdDate = new Date(response.content.createdAt);

                    let createdDateDay = createdDate.getDate();
                    let createdDateMonth = createdDate.getMonth() + 1;
                    let createdDateYear = createdDate.getFullYear();

                    if (createdDateDay < 10) createdDateDay = "0" + createdDateDay;
                    if (createdDateMonth < 10) createdDateMonth = "0" + createdDateMonth;

                    this.userCreatedAt = `${createdDateDay}.${createdDateMonth}.${createdDateYear}`;
                }
            }
        },

        deleteCookies() {
            this.$reset();

            Cookies.remove('logged');
        }
    }
})