<template>
  <div id="videoContainer">
    <div class="spinner" v-show="whichSection === 1"></div>
    <div id="videoSection" v-if="whichSection === 1">
      <iframe
        src="https://www.youtube.com/embed?listType=playlist&list=UULFyFOX7xqRs9RctjQplv5nYw"
        width="100%"
        height="100%"
        frameborder="0"
        allowfullscreen
      >
      </iframe>
    </div>
    <div id="videoForm" v-else>
      <div class="formInput formItem">
        <label for="nickInput">
          Nickname <span class="formError">{{ errors.nickError }}</span>
        </label>
        <div :class="nickClass" class="border">
          <input
            name="nickInput"
            type="text"
            v-model="nickInput"
            maxlength="20"
            autocomplete="off"
          />
          <div class="helpBox" title="Wpisz swój nick">
            <svg-icon :class="nickClass" type="mdi" :path="nickIcon"></svg-icon>
          </div>
        </div>
      </div>
      <div class="formInput formItem">
        <label for="gameInput">
          Gra <span class="formError">{{ errors.gameError }}</span>
        </label>
        <div :class="gameClass" class="border">
          <input
            name="gameInput"
            list="text"
            v-model="gameInput"
            maxlength="20"
            autocomplete="off"
          />
          <div class="helpBox" title="Wpisz grę">
            <svg-icon :class="gameClass" type="mdi" :path="gameIcon"></svg-icon>
          </div>
        </div>
      </div>
      <div class="formInput formItem">
        <label for="linkInput">
          Link <span class="formError">{{ errors.linkError }}</span>
        </label>
        <div :class="linkClass" class="border">
          <input
            name="linkInput"
            type="text"
            v-model="linkInput"
            autocomplete="off"
          />
          <div class="helpBox" title="Wpisz link do filmów">
            <svg-icon :class="linkClass" type="mdi" :path="linkIcon"></svg-icon>
          </div>
        </div>
      </div>
      <div id="formHint">
        <div id="formText">
          <h4>JAK WYSYŁAĆ FILMY?</h4>
          <p>
            Filmy należy wrzucić w JEDNYM FOLDERZE najlepiej na
            <a href="https://drive.google.com">DYSK GOOGLE</a>
            (może być też na innej platformie), następnie folder klikamy PPM
            oraz "Pobierz link do udostępnienia".
          </p>
        </div>
        <div id="formSubmit">
          <button @click="submitForm()" :disabled="isButtonDisabled">
            Wyślij
          </button>
        </div>
      </div>
      <div class="formTextArea formItem">
        <label for="descInput">
          Dodatkowa wiadomość <span id="descSpan">(opcjonalne)</span>
        </label>
        <div class="border">
          <textarea
            name="formTextArea"
            rows="5"
            maxlength="300"
            v-model="descInput"
          ></textarea>
          <div class="helpBox" title="Twoja dodatkowa wiadomość">
            <svg-icon type="mdi" :path="descIcon"></svg-icon>
          </div>
          <span class="letterCount">
            {{ `${lettersCount.descLetters}/${lettersCount.maxDescLetters}` }}
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import svgIcon from "@jamescoyle/vue-icon";
import popupSound from "../assets/audio/popup.wav";

import { useAlertsStore } from "../stores/alertsStore";
import { usePreferenceStore } from "../stores/preferenceStore";
import { useIconsStore } from "../stores/iconsStore";
import { useGeneralStore } from "../stores/generalStore";

export default {
  name: "CarouselContent",
  emits: ["submitForm"],
  props: {
    whichSection: Number,
  },
  components: {
    svgIcon,
  },
  setup() {
    const preferenceStore = usePreferenceStore();
    const alertsStore = useAlertsStore();
    const iconsStore = useIconsStore();
    const generalStore = useGeneralStore();

    return { preferenceStore, alertsStore, iconsStore, generalStore };
  },
  data() {
    return {
      nickIcon: this.iconsStore.questionMarkIcon,
      gameIcon: this.iconsStore.questionMarkIcon,
      linkIcon: this.iconsStore.questionMarkIcon,
      descIcon: this.iconsStore.questionMarkIcon,
      nickInput: "",
      gameInput: "",
      linkInput: "",
      descInput: "",
      nickClass: "",
      gameClass: "",
      linkClass: "",
      isButtonDisabled: true,
      errors: {
        nickError: "",
        gameError: "",
        linkError: "",
      },
      lettersCount: {
        descLetters: 0,
        maxDescLetters: 300,
      },
    };
  },
  methods: {
    async submitForm() {
      if (
        this.nickInput.length == 0 &&
        this.gameInput.length == 0 &&
        this.linkInput.length == 0
      )
        return;

      const formValues = {
        nick: this.nickInput,
        game: this.gameInput,
        link: this.linkInput,
        desc: this.descInput,
      };

      this.nickInput = "";
      this.gameInput = "";
      this.linkInput = "";
      this.descInput = "";

      await this.generalStore
        .sendVideo(formValues)
        .then(async (response) => {
          await this.alertsStore.showNotification(
            response.duration,
            response.status,
            response.message
          );

          if (response.status === "success") {
            this.nickIcon = this.iconsStore.questionMarkIcon;
            this.gameIcon = this.iconsStore.questionMarkIcon;
            this.linkIcon = this.iconsStore.questionMarkIcon;
            this.nickClass = "neutral";
            this.gameClass = "neutral";
            this.linkClass = "neutral";
            this.errors.nickError = "";
            this.errors.gameError = "";
            this.errors.linkError = "";

            this.isButtonDisabled = true;
          }
        })
        .catch((error) => console.log(error));

      if (this.preferenceStore.preferences[0].state) {
        const notificationSound = new Audio(popupSound);
        notificationSound.volume = 0.5;
        notificationSound.play();
      }
    },
    formVerification(value) {
      if (value.length > 0)
        return {
          icon: this.iconsStore.successIcon,
          status: "success",
          error: "",
        };
      else
        return {
          icon: this.iconsStore.alertCircleIcon,
          status: "alert",
          error: "Puste pole!",
        };
    },
    buttonVerification() {
      if (
        this.nickInput.length == 0 ||
        this.gameInput.length == 0 ||
        this.linkInput.length == 0
      ) {
        this.isButtonDisabled = true;
      } else {
        this.isButtonDisabled = false;
      }
    },
  },
  watch: {
    nickInput() {
      this.buttonVerification();
      const data = this.formVerification(this.nickInput);
      this.nickIcon = data.icon;
      this.nickClass = data.status;
      this.errors.nickError = data.error;
    },
    gameInput() {
      this.buttonVerification();
      const data = this.formVerification(this.gameInput);
      this.gameIcon = data.icon;
      this.gameClass = data.status;
      this.errors.gameError = data.error;
    },
    linkInput() {
      this.buttonVerification();
      const data = this.formVerification(this.linkInput);
      this.linkIcon = data.icon;
      this.linkClass = data.status;
      this.errors.linkError = data.error;
    },
    descInput() {
      this.lettersCount.descLetters = this.descInput.length;
    },
  },
};
</script>

<style lang="scss">
#videoContainer {
  width: 100%;
  height: 100%;
  font-size: 20px;

  #videoSection {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
  }

  #videoForm {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    grid-template-rows: repeat(4, auto);
    grid-template-areas:
      "formInput formText"
      "formInput formText"
      "formInput formText"
      "formTextArea formTextArea";
  }
}

#formHint {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  grid-column: 2 / 3;
  grid-row: 1 / 4;

  #formText {
    font-size: 16px;
    padding: 20px;

    h4 {
      font-size: 20px;
      filter: drop-shadow(0 4px 4px #1a1d22);
    }

    a {
      color: #0270d7;
    }

    a:hover {
      color: #005eb6;
    }
  }

  #formSubmit button {
    padding: 5px 15px;
    font-size: 20px;
    color: #fff;
    border: none;
    border: 2px solid #00bcd4;
    border-radius: 14px;
    background: linear-gradient(to bottom right, #9be8e6, #11a7a3);

    &:disabled {
      color: #34373d;
      border: 2px solid transparent;
      background: #474a52;
      cursor: default;
    }
  }

  #formSubmit button:hover {
    border: 2px solid #11a7a3;

    &:disabled {
      color: #34373d;
      border-color: transparent;
      background-color: #474a52;
    }
  }

  #formSubmit button:focus {
    border-color: #9be8e6;
    background: linear-gradient(to bottom right, #008e8b, #00514f);
  }
}

.formItem {
  margin: 7px;
}

.formInput,
.formTextArea {
  display: flex;
  flex-flow: column nowrap;
  padding: 5px 10px 10px;
  border: 1px solid #1d2026;
  border-radius: 5px;
  background-color: #282c35;
  box-shadow: 0 4px 4px #1a1d22;

  .helpBox {
    display: contents;
    cursor: help;
  }

  label {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-right: 10px;

    .formError {
      font-size: 15px;
      animation: formErrorPulse 2s ease-in-out infinite alternate;
    }

    @keyframes formErrorPulse {
      0% {
        color: #b90e0e;
        transform: scale(0.95);
      }

      70% {
        color: #ee3535;
        transform: scale(1);
      }

      100% {
        color: #b90e0e;
        transform: scale(0.95);
      }
    }

    #descSpan {
      font-size: 15px;
      color: #48d1fc;
      text-align: center;
      margin-left: 5px;
    }
  }

  .border {
    position: relative;
    display: flex;
    flex-flow: row nowrap;
    width: 100%;
    height: 100%;
    margin: 5px auto 0;
    padding: 5px;
    color: #0270d7;
    border: 1px solid;
    border-radius: 5px;
    background-color: #23272e;
    transition: 0.2s ease-in-out;

    &.success {
      color: #63cf5a;
    }

    &.alert {
      color: #d76161;
    }

    input,
    textarea,
    p {
      width: 100%;
      margin: 0;
      padding: 0;
      text-align: left;
      background: none;
      outline: none;
      border: none;
      color: #fff;
      font-size: 20px;
      font-family: "Lato", sans-serif;
    }
  }
}

.formInput {
  &:nth-of-type(1) {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }
  &:nth-of-type(2) {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
  }
  &:nth-of-type(3) {
    grid-column: 1 / 2;
    grid-row: 3 / 4;
  }
}

.formTextArea {
  grid-column: 1 / 3;
  grid-row: 4 / 5;

  .border {
    color: #0270d7;
  }
  textarea {
    resize: none;
    padding: 10px;
  }
  textarea::-webkit-scrollbar {
    display: none;
  }
}
</style>
