<template>
  <div
    id="notification"
    v-if="alertsStore.alerts.isDisplay"
    :style="alertsStore.alerts.styles"
    :class="alertsStore.alerts.notificationClass"
  >
    <div id="iconContainer">
      <svg-icon
        width="30"
        height="30"
        type="mdi"
        :path="alertsStore.alerts.notificationIcon"
      ></svg-icon>
    </div>
    <h1>{{ alertsStore.alerts.notificationMsg }}</h1>
  </div>
</template>

<script>
import svgIcon from "@jamescoyle/vue-icon";

import { useAlertsStore } from "../../stores/alertsStore";

export default {
  name: "NotificationContainer",
  components: {
    svgIcon,
  },
  setup() {
    const alertsStore = useAlertsStore();

    return { alertsStore };
  },
  data() {
    return {
      isDisplay: false,
      styles: {
        top: "-75px",
      },
      notificationIcon: "",
      notificationClass: "",
      notificationMsg: "",
    };
  },
};
</script>

<style lang="scss" scoped>
#notification {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
  flex-flow: row nowrap;
  width: 350px;
  height: 50px;
  margin: auto;
  border-radius: 5px;
  border-left: 5px solid;
  background-color: #282c35;
  transition: 0.5s ease-in-out;

  #iconContainer {
    padding: 0 10px;
    border-right: 2px solid #3a4348;
  }

  h1 {
    margin: auto;
    font-size: 20px;
    font-weight: bold;
  }

  &.success {
    $color: #63cf5a;
    border-color: $color;

    svg {
      color: $color;
      filter: drop-shadow(0 0 4px $color);
    }
  }

  &.info {
    $color: #0270d7;
    border-color: $color;

    svg {
      color: $color;
      filter: drop-shadow(0 0 4px $color);
    }
  }

  &.warning {
    $color: #bcb043;
    border-color: $color;

    svg {
      color: $color;
      filter: drop-shadow(0 0 4px $color);
    }
  }

  &.error {
    $color: #c71a1a;
    border-color: $color;

    svg {
      color: $color;
      filter: drop-shadow(0 0 4px $color);
    }
  }

  svg {
    animation: pulse 2s ease-in-out infinite normal;
  }

  @keyframes pulse {
    0% {
      transform: scale(1);
    }
    75% {
      transform: scale(1.1);
    }
    100% {
      transform: scale(1);
    }
  }
}
</style>