<template>
  <main>
    <header id="carouselHeader">
      <h1 v-if="activeSection === 1">Youtube</h1>
      <h1 v-else>Formularz</h1>
      <hr id="afterTitle" />
      <span v-if="activeSection === 1">Najlepszy kanał na YT :)</span>
      <span v-else>Udostępnij swoje filmy</span>
      <hr id="underTitle" />
    </header>
    <section id="carouselSection">
      <div id="carouselArrow">
        <div @click="activeSection--" class="arrowLeft pointer">
          <svg-icon
            width="50px"
            height="50px"
            type="mdi"
            :path="iconsStore.mdiChevronLeft"
          ></svg-icon>
        </div>
        <div @click="activeSection++" class="arrowRight pointer">
          <svg-icon
            width="50px"
            height="50px"
            type="mdi"
            :path="iconsStore.mdiChevronRight"
          ></svg-icon>
        </div>
      </div>
      <CarouselContent :whichSection="activeSection" />
    </section>
    <div id="carouselButtons">
      <button
        @click="activeSection = 1"
        class="carouselButton"
        :class="{ active: activeSection === 1 }"
      ></button>
      <button
        @click="activeSection = 2"
        class="carouselButton"
        :class="{ active: activeSection === 2 }"
      ></button>
    </div>
  </main>
</template>

<script>
import svgIcon from "@jamescoyle/vue-icon";
import { useIconsStore } from "../stores/iconsStore";

import CarouselContent from "./CarouselContent.vue";

export default {
  name: "MainSection",
  components: {
    svgIcon,
    CarouselContent,
  },
  setup() {
    const iconsStore = useIconsStore();

    return { iconsStore };
  },
  data() {
    return {
      activeSection: 1,
    };
  },
  watch: {
    activeSection(newValue) {
      if (newValue < 1) this.activeSection = 2;
      else if (newValue > 2) this.activeSection = 1;
    },
  },
};
</script>

<style lang="scss" scoped>
$transition: 0.3s ease-in-out;

main {
  position: absolute;
  bottom: 100px;
  right: 200px;

  #carouselHeader {
    margin: 0 0 15px 10px;
    display: flex;
    flex-flow: row wrap;
    font-weight: bold;
    justify-content: flex-start;
    align-items: center;

    h1 {
      margin: 0;
      color: #3b90e0;
      font-size: 36px;
    }
    #afterTitle {
      width: 2px;
      height: 35px;
      margin: 0 15px;
      border: none;
      background: linear-gradient(
        to bottom,
        rgb(16, 61, 105),
        rgba(29, 32, 38, 0)
      );
    }
    span {
      font-size: 20px;
    }
    #underTitle {
      width: 75%;
      height: 2px;
      margin: 0;
      border: none;
      background: linear-gradient(
        to right,
        rgb(33, 107, 176, 0.75),
        rgb(29, 32, 38)
      );
    }
  }
  #carouselSection {
    width: 800px;
    height: 500px;
    border: 10px solid #1f2329;
    border-radius: 1px;
    background-color: #23272e;
    box-shadow: 0 4px 4px #16181d;
  }

  #carouselArrow {
    position: relative;
    top: 45%;
    user-select: none;

    div {
      position: absolute;
      display: inline;
      transition: $transition;

      &:hover {
        color: #3b90e0;
      }
    }
    .arrowLeft {
      left: -55px;
    }
    .arrowRight {
      right: -55px;
    }
  }

  #carouselButtons {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    width: 50px;
    margin: 10px auto;

    .carouselButton {
      width: 10px;
      height: 10px;
      padding: 0;
      border: none;
      border-radius: 50%;
      background-color: #23272e;
      box-shadow: 0 4px 4px #16181d;
      transition: $transition;

      &:hover {
        background-color: #0c447b;
      }
    }

    .active {
      background-color: #0c447b;
    }
  }
}
</style>