<template>
  <nav id="homeButtonsContainer">
    <a
      :href="'https://www.youtube.com/@Dejku'"
      target="_blank"
      rel="noopener noreferrer"
      class="homeButton"
    >
      <svg-icon type="mdi" :path="iconsStore.mdiYoutube"></svg-icon>
      <span>Youtube</span>
    </a>
    <button @click="homeButton()" class="homeButton">
      <svg-icon class="login" type="mdi" :path="iconsStore.mdiLogin"></svg-icon>
      <span>{{ accountStore.isLogged ? "Dashboard" : "Logowanie" }}</span>
    </button>
  </nav>
</template>

<script>
import svgIcon from "@jamescoyle/vue-icon";

import { useIconsStore } from "../stores/iconsStore";
import { useLoginStore } from "../stores/loginStore";
import { useAccountStore } from "../stores/accountStore";

export default {
  name: "HomeButtons",
  components: {
    svgIcon,
  },
  setup() {
    const iconsStore = useIconsStore();
    const loginStore = useLoginStore();
    const accountStore = useAccountStore();

    return { iconsStore, loginStore, accountStore };
  },
  methods: {
    homeButton() {
      if (this.accountStore.isLogged) this.$router.push("/dashboard");
      else this.loginStore.isShow = true;
    },
  },
};
</script>

<style lang="scss" scoped>
#homeButtonsContainer {
  position: absolute;
  width: 100px;
  bottom: 50px;
  left: 50px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;

  .homeButton {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 15px;
    width: 45px;
    height: 45px;
    padding: 10px;
    color: #fff;
    border: none;
    border-radius: 25px;
    background: linear-gradient(to bottom right, #3b90e0, #224f7a);
    transition: 0.3s ease-in-out;
